<template>
  <div class="hello">
    <template v-if="!big">
      <img class="img" :src="src" @click="big = true" />
      <button style="height: 50px;width: 50px" @click="meow">{{ load ? `load` : 'meow' }}</button>
      <div>{{percent * 100}}</div>
      <div>{{ error }}</div>
    </template>
    <template v-else>
      <img class="big-img" :src="src" @click="big = false" />
    </template>
  </div>
</template>

<script>
import cos from 'cos-js-sdk-v5';

export default {
  name: 'HelloWorld',
  props: ['sid', 'skey', 'password', 'prefix', 'meowbreak', 'region', 'bucket'],
  data() {
    return {
      src: '',
      cos: null,
      big: false,
      list: [],
      load: false,
      percent: '',
      error: ''
    }
  },
  mounted() {
    this.cos = new cos({
      SecretId: this.sid,
      SecretKey: this.skey,
    });
    this.cos.getBucket({
      Bucket: this.bucket,
      Region: this.region,
    }).then(({ Contents }) => {
      this.list = Contents.map(item => item.Key);
      this.meow();
    });
  },
  methods: {
    getOne() {
      const length = this.list.length;
      const random = (Math.floor(Math.random() * length));
      return this.list.splice(random, 1)[0];
    },
    meow() {
      if (this.load) return;
      this.load = true;
      this.percent = '';
      this.error = '';
      const key = this.getOne();
      this.cos.getObject({
        Bucket: this.bucket,
        Region: this.region,
        Key: key,
        onProgress: ({ percent }) => {this.percent = percent},
      }).then(({ Body }) => {
        try {
          this.src = `${this.prefix}${this.meowbreak(Body.toString(), this.password)}`;
        } catch (e) {
          console.log(e);
          this.error = key;
        }
        this.load = false;
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {
  overflow: auto;
}
.img {
  width: 100vw;
  height: auto;
}
.big-img {
  min-height: 100vh;
}
</style>
