<template>
  <div id="app">
    <div v-if="!confirm" class="form">
    <input v-model="text" />
    <input v-model="key" />
    <button @click="meow">meow</button>
    </div>
    <HelloWorld v-else :sid="sid" :skey="skey" :password="password" :prefix="prefix" :region="region" :bucket="bucket" :meowbreak="meowbreak" />
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'
import CryptoJS from 'crypto-js';

export default {
  name: 'App',
  components: {
    HelloWorld
  },
  data() {
    return {
      confirm: false,
      text: '',
      password: '',
      key: '',
      sid: '',
      skey: '',
      prefix: '',
      region: '',
      bucket: '',
    }
  },
  methods: {
    meowbreak(word, key) {
      return CryptoJS.AES.decrypt(word, key).toString(CryptoJS.enc.Utf8);
    },
    meow() {
      let text = this.text.split('');
      const aim = text.splice(-this.key.length).join('');
      const dataList = text.join('').split(aim);
      this.sid = dataList[0];
      this.skey = dataList[1];
      this.password = dataList[2];
      this.prefix = dataList[3];
      this.region = dataList[4];
      this.bucket = dataList[5];

      this.password = this.meowbreak(this.password, this.key);
      this.sid = this.meowbreak(this.sid, this.key);
      this.skey = this.meowbreak(this.skey, this.key);
      this.prefix = this.meowbreak(this.prefix, this.key);
      this.region = this.meowbreak(this.region, this.key);
      this.bucket = this.meowbreak(this.bucket, this.key);

      this.confirm = true;
    }
  }
}
</script>

<style>
body {
  margin: 0;
}
.form {
  display: flex;
  flex-direction: column;
}
</style>
